.layout_ef .tips {
  color: #bbbbbb;
  font-size: 12px;
}
.layout_ef .cardtitle {
  font-size: 14px;
}
.layout_ef .cardtitle::before {
  content: "";
  background-color: #5582F3;
  padding-right: 2px;
  margin-right: 10px;
  border-radius: 2px;
}
.layout_ef .card {
  padding: 8px 0px;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.layout_ef .card .card_li {
  background-color: #F2F9FF;
  border-radius: 6px;
  width: 239px;
  padding: 16px;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  cursor: pointer;
  margin-right: 10px;
  margin-bottom: 10px;
  flex: 0 0 auto;
  flex-shrink: 0;
}
.layout_ef .card .card_li .card_li_left {
  width: 14px;
  height: 14px;
}
.layout_ef .card .card_li .card_li_right {
  padding-left: 8px;
  padding-right: 16px;
  width: -webkit-fill-available;
  flex-shrink: 0;
}
.layout_ef .card .card_li .card_li_right .card_li_right_header {
  font-family: MicrosoftYaHeiSemibold;
  font-size: 14px;
  color: #212121;
  letter-spacing: 0;
  font-weight: 600;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  word-wrap: break-word;
}
.layout_ef .card .card_li .card_li_right .card_li_right_content {
  font-family: MicrosoftYaHei;
  font-size: 14px;
  color: #666666;
  letter-spacing: 0;
  font-weight: 400;
  margin-top: 18px;
  display: -webkit-box;
  text-overflow: ellipsis;
  overflow: hidden;
  word-wrap: break-word;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  height: 36px;
  line-height: 18px;
  width: 185px;
}
.layout_ef .card .card_li .card_li_right .card_li_right_footer {
  margin-top: 8px;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  align-items: center;
  vertical-align: middle;
}
.layout_ef .card .card_li .card_li_right .card_li_right_footer .card_li_right_footer_text {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #276FF5;
  letter-spacing: 0;
  font-weight: 400;
}
.layout_ef .card .card_li .card_li_right .card_li_right_footer .card_li_right_footer_icon {
  color: #276FF5;
}
.layout_ef .card .card_li:hover {
  border: #276FF5 1px solid;
}
.layout_ef .card .card_empty {
  width: 100%;
  padding: 20px 0px;
}
.layout_ef .card_more {
  font-family: MicrosoftYaHei;
  font-size: 12px;
  color: #5582F3;
  letter-spacing: 0;
  line-height: 18px;
  font-weight: 400;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  align-items: center;
  vertical-align: middle;
  cursor: pointer;
}
.layout_ef .card_more .card_more_icon {
  margin-left: 5px;
}
